import {resolve} from 'path';
import {MessageChannel, MessagePort, Worker, isMainThread, workerData} from 'worker_threads';
import {extractLocalImportContext} from './localImportContext';
import {NodePosition} from './parse';
import {DocumentInfo, DocumentInfoWithOffset, PromptInfo, PromptOptions} from './prompt';
import {getNeighborSnippets} from './snippetInclusion/neighboringFiles';

// function used for testing
export function sleep(delay: number): Promise<string> {
    return new Promise(resolve => {
        var start = new Date().getTime();
        while (new Date().getTime() < start + delay);
        resolve(`delay: ${delay}`);
    });
}

type PromiseCallbacks = {resolve: Function; reject: Function};

export const workerFns = ['getNeighborSnippets', 'extractLocalImportContext', 'sleep'] as const;
export type ProxiedFunction = (typeof workerFns)[number];
/** The interface types appearing in argument or return types of proxiable functions. */
type ProxiedInterface = NodePosition | DocumentInfo | DocumentInfoWithOffset | PromptInfo | PromptOptions;
/**
 * A type that can be copied using the [structured clone algorithm](https://developer.mozilla.org/en-US/docs/Web/API/Web_Workers_API/Structured_clone_algorithm).
 *
 * Note: This is an approximation; it doesn't currently seem to be possible to capture this precisely
 * (https://github.com/microsoft/TypeScript/issues/29941).
 */
type CloneableType =
    // primitives
    | boolean
    | number
    | string
    | undefined
    // objects with cloneable properties
    | {[key in keyof ProxiedInterface]: CloneableType}
    // arrays with clonable elements
    | CloneableType[];

/**
 * A class to manage a worker thread and to proxy functions for execution on the worker thread
 *
 * The class gets instantiated on the main thread and is by deafult "not threaded". When threading
 * is turned on, the class creates a worker thread containing another instance of itself and does the following:
 *   - the main thread instance overwrites its own methods with the `proxy` functions that delegate to the
 *     functions on the worker instance (this happens according to the `workerFns` list)
 *   - listeners are configured on the worker thread to receive messages from the main thread
 *     containing the names of the functions to execute and their arguments
 *   - listeners are configured on the main thread to receive messages from the worker thread
 *     containing the outputs of the proxy functions
 *
 */

export class WorkerProxy {
    private nextHandlerId = 0;
    private handlers = new Map<number, PromiseCallbacks>();
    fns = new Map<string, Function>();
    declare worker: Worker | undefined;
    declare port: MessagePort;

    // proxy functions
    extractLocalImportContext = extractLocalImportContext;
    getNeighborSnippets = getNeighborSnippets;
    sleep = sleep;
    // proxy functions end

    constructor() {
        if (!isMainThread && workerData) {
            // invoke on the worker thread only
            this.configureWorkerResponse(workerData.port);
        }
    }

    initWorker() {
        const {port1, port2} = new MessageChannel();
        this.port = port1;
        this.worker = new Worker(resolve(__dirname, '..', 'dist', 'workerProxy.js'), {
            // port2 is the port on which the worker will respond to messages
            workerData: {port: port2},
            transferList: [port2],
        });

        // Add listeners to the main thread
        this.port.on('message', m => this.handleMessage(m));
        this.port.on('error', e => this.handleError(e));
    }

    startThreading() {
        if (this.worker) {
            throw new Error('Worker thread already initialized.');
        }
        this.proxyFunctions();
        this.initWorker();
    }

    stopThreading() {
        if (this.worker) {
            this.worker.terminate();
            this.worker.removeAllListeners();
            this.worker = undefined;
            this.unproxyFunctions();
            this.handlers.clear();
        }
    }

    proxyFunctions() {
        for (const fn of workerFns) {
            // preserve references to original functions
            this.fns.set(fn, this[fn]);
            this.proxy(fn);
        }
    }

    unproxyFunctions() {
        for (const fn of workerFns) {
            const originalFn = this.fns.get(fn) as WorkerProxy[typeof fn];
            if (originalFn) {
                (this[fn] as Function) = originalFn;
            } else {
                throw new Error(`Unproxy function not found: ${fn}`);
            }
        }
    }

    /**
     * the function that executes on the worker thread
     * sets up listener on the worker to respond to messages sent from the main thread
     */
    configureWorkerResponse(port: MessagePort) {
        this.port = port;
        this.port.on('message', async ({id, fn, args}: {id: number; fn: ProxiedFunction; args: CloneableType[]}) => {
            const proxiedFunction = this[fn];
            if (!proxiedFunction) {
                throw new Error(`Function not found: ${fn}`);
            }
            try {
                let res = await (proxiedFunction as Function).apply(this, args as Parameters<typeof proxiedFunction>);
                this.port.postMessage({id, res});
            } catch (err) {
                this.port.postMessage({id, err});
            }
        });
    }

    /**
     * resolve the promise associated with the message id sent to the worker
     */
    handleMessage({id, err, res}: {id: number; err: Error; res: any}) {
        const handler = this.handlers.get(id);
        if (handler) {
            this.handlers.delete(id);
            if (err) {
                handler.reject(err);
            } else {
                handler.resolve(res);
            }
        }
    }

    handleError(maybeError: unknown) {
        console.log(maybeError);
        // add the local stack trace to the error so the uncaught exception handler can log where it originated
        let err;
        if (maybeError instanceof Error) {
            err = maybeError;
            const ourStack = new Error().stack;
            if (err.stack && ourStack?.match(/^Error\n/)) {
                err.stack += ourStack.replace(/^Error/, '');
            }
        } else {
            err = new Error(`Non-error thrown: ${maybeError}`);
        }
        for (const handler of this.handlers.values()) {
            handler.reject(err);
        }
        throw err;
    }

    /**
     * Wrap function `fn` into a proxy that sends a message
     * with the function name and arguments to the worker and returns a promise that will be
     * resolved with the result the worker returns or rejected with any exception the worker
     * encounters.
     *
     * The wrapped function must return a promise, and its arguments and return values must be
     * primitive values or objects that can be serialized by the worker-threads library.
     */
    proxy<T extends ProxiedFunction>(fn: T) {
        this[fn] = function <R extends CloneableType>(...args: CloneableType[]): Promise<R> {
            const id = this.nextHandlerId++;
            return new Promise((resolve, reject) => {
                this.handlers.set(id, {resolve, reject});
                this.port?.postMessage({id, fn, args});
            });
        };
    }
}

export let workerProxy = new WorkerProxy();

// for testing we need to reset the singleton instance
export const reset = () => {
    workerProxy.stopThreading();
    workerProxy = new WorkerProxy();
};
