import {ok} from 'assert';
import {DocumentInfo, DocumentInfoWithOffset, SnippetSelectionOption} from '../prompt';
import {CursorHistoryMatcher, CursorSnippetsPickingStrategy} from './cursorMatching';
import {FixedWindowSizeJaccardMatcher, FunctionJaccardMatcher} from './jaccardMatching';
import {SnippetWithProviderInfo} from './snippets';

export enum NeighboringTabsOption {
    None = 'none',
    Conservative = 'conservative', // set jaccard threshold 0.3
    Medium = 'medium', // set jaccard threshold 0.1
    Eager = 'eager', // set jaccard threshold 0.0
    EagerButLittle = 'eagerButLittle',
    EagerButMedium = 'eagerButMedium',
    EagerButMuch = 'eagerButMuch',
    RetrievalComparable = 'retrievalComparable',
}

export enum NeighboringSnippetType {
    NeighboringFunctions = 'neighboringFunction',
    NeighboringSnippets = 'neighboringSnippet',
    CursorHistoryMatcher = 'cursorhistorymatcher',
}

interface NeighborSelection {
    snippetLength: number;
    threshold: number;
    numberOfSnippets: number;
}
// * @param lineCursorHistory - A The first key is the file name, and the second key
// *                            is the line number, the value is the number of cursor
// *                            focused.
export type LineCursorHistory = Map<string, Map<number, number>>;

export const neighborOptionToSelection: Record<NeighboringTabsOption | string, NeighborSelection> = {
    none: {
        snippetLength: 1,
        threshold: -1,
        numberOfSnippets: 0,
    },
    conservative: {
        snippetLength: 10,
        threshold: 0.3,
        numberOfSnippets: 1,
    },
    medium: {
        snippetLength: 20,
        threshold: 0.1,
        numberOfSnippets: 2,
    },
    eager: {
        snippetLength: 60,
        threshold: 0,
        numberOfSnippets: 4,
    },
    eagerButLittle: {
        snippetLength: 10,
        threshold: 0,
        numberOfSnippets: 1,
    },
    eagerButMedium: {
        snippetLength: 20,
        threshold: 0,
        numberOfSnippets: 4,
    },
    eagerButMuch: {
        snippetLength: 60,
        threshold: 0,
        numberOfSnippets: 6,
    },
    retrievalComparable: {
        snippetLength: 30,
        threshold: 0,
        numberOfSnippets: 4,
    },
};

// Sanity thresholds on the number of neighbors to consider
const MAX_CHARACTERS_PER_FILE = 10000;
const MAX_NUMBER_OF_FILES = 20;

function getMatcher(
    doc: DocumentInfoWithOffset,
    neighboringSnippetTypes: NeighboringSnippetType,
    selection: NeighborSelection,
    lineCursorHistory?: Map<string, Map<number, number>>,
    cursorSnippetsPickingStrategy: CursorSnippetsPickingStrategy = CursorSnippetsPickingStrategy.CursorJaccard
) {
    let matcherFactory;
    if (neighboringSnippetTypes === NeighboringSnippetType.NeighboringSnippets) {
        matcherFactory = FixedWindowSizeJaccardMatcher.FACTORY(selection.snippetLength);
    } else if (neighboringSnippetTypes === NeighboringSnippetType.NeighboringFunctions) {
        // To be removed in https://github.com/github/copilot-client/pull/3448
        matcherFactory = FunctionJaccardMatcher.FACTORY(selection.snippetLength);
    } else {
        ok(lineCursorHistory !== undefined, 'lineCursorHistory should not be undefined');
        matcherFactory = CursorHistoryMatcher.FACTORY(
            selection.snippetLength,
            lineCursorHistory,
            cursorSnippetsPickingStrategy
        );
    }
    return matcherFactory.to(doc);
}

/**
 * @returns A SnippetWithProviderInfo describing the best matches from neighbors.
 */
export async function getNeighborSnippets(
    doc: DocumentInfoWithOffset,
    neighbors: DocumentInfo[],
    neighboringSnippetTypes: NeighboringSnippetType,
    options: NeighboringTabsOption | string,
    snippetSelectionOption?: SnippetSelectionOption,
    snippetSelectionK?: number,
    lineCursorHistory?: LineCursorHistory,
    cursorSnippetsPickingStrategy?: CursorSnippetsPickingStrategy
): Promise<SnippetWithProviderInfo[]> {
    const selection = {...neighborOptionToSelection[options]};
    const matcher = getMatcher(
        doc,
        neighboringSnippetTypes,
        selection,
        lineCursorHistory,
        cursorSnippetsPickingStrategy
    );
    if (selection.numberOfSnippets === 0) {
        return [];
    }

    const snippets = (
        await neighbors
            // filter out absurdly long or absurdly many open files
            .filter(neighbor => neighbor.source.length < MAX_CHARACTERS_PER_FILE && neighbor.source.length > 0)
            // slice(0) duplicates an array
            .slice(0, MAX_NUMBER_OF_FILES)
            .reduce(
                async (
                    acc,
                    neighbor // accumulator of all snippets from all neighbors
                ) =>
                    (await acc).concat(
                        (await matcher.findMatches(neighbor, snippetSelectionOption, snippetSelectionK)).map(
                            snippet => ({relativePath: neighbor.relativePath, ...snippet})
                        )
                    ),
                Promise.resolve([] as SnippetWithProviderInfo[])
            )
    )
        .filter(
            neighbor =>
                // remove files that had no match at all
                neighbor.score &&
                neighbor.snippet &&
                // remove files that had a low score
                neighbor.score > selection.threshold
        )
        // order them with best (highest scores) last
        .sort((a, b) => a.score - b.score)
        // take the best options from the end
        .slice(-selection.numberOfSnippets);
    return snippets;
}
